function cyc_stegger(option = {}) {
  const LENGTHSIZE = 24; //
  const MARKSIZE = 8;//
  const CHARSIZE = option.charSize || 16; //,16,8
  const MIXCOUNT = option.mixCount || 2; //

  const textMark = 'text';//
  const fileMark = 'file';//

  let offset = 0; //
  //const typeSize = 24;//
  this.colors = []; //imageData
  //
  this.restAllData = function () {
    offset = 0;
    this.colors = [];
  };
  /**
   * location
   * 2,2,33
   * 2,2
   * & 00 10
   * number 
   * location 
   * bit 
   */
  this.setBit = function (number, location, bit) {
    return (number & ~(1 << location)) | (bit << location);
  };
  this.getBit = function (number, location) {
    return (number >> location) & 1;
  };
  /**
   * 
   */
  this.getBitsFromHeadNumber = function (number, size) {
    return number
      .toString(2)
      .padStart(size, 0)
      .split("")
      .reverse();
  };
  /**
   * 
   * number 
   * size bit 16,16
   */
  this.writeNumber = function (number, size) {
    let bits = this.getBitsFromHeadNumber(number, size); // getBitsFromNumber(number, size);
    let pos = 0; //
    let mix = 0; //
    //
    while (pos < bits.length && offset < this.colors.length) {
      this.colors[offset] = this.setBit(
        this.colors[offset],
        mix++,
        bits[pos++]
      );
      //MIXCOUNT 2,255 25412532
      while (mix < MIXCOUNT && pos < bits.length) {
        this.colors[offset] = this.setBit(
          this.colors[offset],
          mix++,
          bits[pos++]
        );
      }
      offset++; //
      mix = 0; //
      // set the alpha value in this pixel to 255
      // we have to do this because browsers do premultiplied alpha
      // see for example: http://stackoverflow.com/q/4309364
      //aplha:alphaputImageData()getImageData()
      //alpha25564
      if ((offset + 1) % 4 == 0) {
        this.colors[offset] = 255;
        offset++;
      }
    }
  };
  /**
   * 
   */
  this.readNumber = function (size) {
    let bits = [];
    let pos = 0;
    let number = 0; //
    let mix = 0; //
    // console.log(pos +"<"+ size +"&&"+ offset +'<'+ this.colors.length)
    while (pos < size && offset < this.colors.length) {
      let bit = this.getBit(this.colors[offset], mix++);
      number = this.setBit(number, pos++, bit);
      bits.push(bit);
      while (mix < MIXCOUNT && pos < size) {
        bit = this.getBit(this.colors[offset], mix++);
        number = this.setBit(number, pos++, bit);
        bits.push(bit);
      }
      offset++;
      mix = 0;
      if ((offset + 1) % 4 == 0) {
        offset++;
      }
    }
    return number;
  };
  /**
   * 
   */
  this.createFileBlob = function (data) {
    const file = new window.Blob([data], { type: "application/octet-binary" })
    const fileUrl = window.URL.createObjectURL(file)
    // const files = document.getElementById('files')
    // const link = document.createElement('a')
    // link.setAttribute('href', fileUrl)
    // link.setAttribute('download', name)
    // link.innerText = name + ' - Size: ' + file.size
    // files.appendChild(link);
    return fileUrl
  }
  //
  this.encrypt = function (src, canvas, text) {
    return new Promise((resolve, reject) => {
      let img = new Image();
      img.src = src;
      img.onload = () => {
        this.restAllData();
        //,
        canvas.width = img.width;
        canvas.height = img.height;
        let ctx = canvas.getContext("2d");
        //
        const pixelCount = ctx.canvas.width * ctx.canvas.height;
        // canvas,
        ctx.drawImage(img, 0, 0);
        // console.log(img.width,img.height)
        let imgData = ctx.getImageData(0, 0, img.width, img.height);
        this.colors = imgData.data; //

        if (typeof text == 'string') {//
          text = text + "";
          if (LENGTHSIZE + text.length * CHARSIZE > pixelCount * 3 * MIXCOUNT) {
            reject("");
            return
          }
          //
          this.writeNumber(text.length, LENGTHSIZE);
          //
          this.writeNumber(textMark, MARKSIZE);
          //
          for (let i = 0; i < text.length; i++) {
            this.writeNumber(text.charCodeAt(i), CHARSIZE);
          }
          //
          ctx.putImageData(imgData, 0, 0);
          resolve(canvas.toDataURL());
        } else {//
          let fileReader = new FileReader();
          fileReader.addEventListener("loadend", (event) => {
            let data = new Uint8Array(event.target.result);
            let fileName = text.name;
            console.log(fileName)
            //
            this.writeNumber(data.length, LENGTHSIZE);
            this.writeNumber(fileMark, MARKSIZE);//
            this.writeNumber(fileName.length, LENGTHSIZE); //
            for (let i = 0; i < fileName.length; i++) {
              this.writeNumber(fileName.charCodeAt(i), CHARSIZE);
            }
            for (let i = 0; i < data.length; i++) {
              this.writeNumber(data[i], 8);
            }
            ctx.putImageData(imgData, 0, 0);
            //
            resolve(canvas.toDataURL());
          });
          fileReader.readAsArrayBuffer(text);

        }

      };
    });
  };
  //
  this.solution = function (src2, canvas) {
    return new Promise(resolve => {
      let img2 = new Image();
      img2.src = src2; //'./red2.jpg';
      img2.onload = () => {
        this.restAllData();
        canvas.width = img2.width;
        canvas.height = img2.height;
        let ctx = canvas.getContext("2d");
        const pixelCount = ctx.canvas.width * ctx.canvas.height;
        // canvas,
        ctx.drawImage(img2, 0, 0);
        let imgData = ctx.getImageData(0, 0, img2.width, img2.height);
        this.colors = imgData.data; //
        // console.log(this.colors)
        //
        const fileLength = this.readNumber(LENGTHSIZE);
        console.log(fileLength)
        //
        const dataType = this.readNumber(MARKSIZE);
        console.log(dataType)
        if (dataType == textMark) {//
          if (LENGTHSIZE + fileLength * CHARSIZE > pixelCount * 3 * MIXCOUNT) {
            return "";
          }
          if (fileLength <= 0) {
            return "";
          }
          let text = [];
          for (let i = 0; i < fileLength; i++) {
            let code = this.readNumber(CHARSIZE);
            // console.log(code)
            text.push(String.fromCharCode(code));
          }
          resolve({
            type: dataType,
            data: text.join("")
          });
        } else {
          //
          const filenamelength = this.readNumber(LENGTHSIZE);
          console.log(filenamelength)
          //
          let filenametext = []
          for (let i = 0; i < filenamelength; i++) {
            let code = this.readNumber(CHARSIZE);
            filenametext.push(String.fromCharCode(code));
          }
          console.log(filenametext.join(''))
          //
          let buffer = new ArrayBuffer(fileLength);
          let filedata = new Uint8Array(buffer);
          for (let i = 0; i < fileLength; i++) {
            let b = this.readNumber(8);
            filedata[i] = b;
          }
          resolve({
            type: dataType,
            filename: filenametext.join(''),
            url: this.createFileBlob(filedata)
          });
        }
      };
    });
  };
  //
  this.downloadFile = function (fileName, canvas) {
    let aLink = document.createElement("a");
    // console.log(this.colors)
    canvas.toBlob(function (blob) {
      //let blob = this.base64ToBlob(content); //new Blob([content]);
      console.log(blob);
      let evt = document.createEvent("HTMLEvents");
      evt.initEvent("click", true, true); //initEvent FF  
      aLink.download = fileName;
      aLink.href = URL.createObjectURL(blob);

      // aLink.dispatchEvent(evt);
      aLink.click();
    });
  };
  //base64blob
  this.base64ToBlob = function (code) {
    let parts = code.split(";base64,");
    let contentType = parts[0].split(":")[1];
    let raw = window.atob(parts[1]);
    let rawLength = raw.length;

    let uInt8Array = new Uint8Array(rawLength);

    for (let i = 0; i < rawLength; ++i) {
      uInt8Array[i] = raw.charCodeAt(i);
    }
    return new Blob([uInt8Array], { type: contentType });
  };
}

export default new cyc_stegger();
